.scroll-up {
  position: fixed;
  right: 50px;
  bottom: 50px;
  z-index: 1200;
  width: 50px;
  height: 50px;
  color: $accent-color;
  cursor: pointer;
  visibility: hidden;
  border-radius: 50px;
  box-shadow: 0 0 0 4px $card-desc-color;
  opacity: 0;
  transition: 0.2s;
  transform: translateY(25px);
}
.scroll-up::before {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  font-size: 30px;
  content: '↑';
}

.scroll-up:hover {
  color: orange;
}
.scroll-up-active {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}
.scroll-up-path {
  box-sizing: border-box;
  fill: none;
  stroke: $accent-color;
  stroke-width: 4px;
  transition: 0.2s;
}
